// Starter styles
html, body, h1, h2, h3, h4, h5 {
    margin: 0;
    padding: 0;
}

body {
    background-color: #f5f5f5;
    color: $color-background-color;
    font-size: 16px;
    font-family: $font-family-sans-serif;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
