.mln__expand-btn {
  position: relative;
  z-index: 1;
  overflow: hidden;
  width: 2.813rem;
  height: 3rem;
  border: 0;
  background-color: #2C3230;
  color: white;
  text-indent: -9999px;
  font-size: 0;
  cursor: pointer;
}

.mln__expand-btn__burger {
  margin-right: auto;
  margin-left: auto;
  width: 1.438rem;
  height: 0.938rem;
}
.mln__expand-btn__burger:after, .mln__expand-btn__burger:before {
  position: absolute;
  content: "";
}
.mln__expand-btn__burger:before {
  top: -0.375rem;
}
.mln__expand-btn__burger:after {
  bottom: -0.188rem;
}

.mln__expand-btn__burger,
.mln__expand-btn__burger:before,
.mln__expand-btn__burger:after {
  position: relative;
  display: block;
  width: 1.2rem;
  height: 0.188rem;
  border-radius: 0;
  background-color: white;
}

.mln__toggle-btn,
.mln__toggle-indicator {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  overflow: hidden;
  width: 2.813rem;
  height: 3rem;
  border: 0;
  border-radius: 0;
  background-color: #202423;
  color: white;
  vertical-align: middle;
  text-align: center;
  cursor: pointer;
  -webkit-appearance: none;
}
.mln__toggle-btn:hover, .mln__toggle-btn:focus,
.mln__toggle-indicator:hover,
.mln__toggle-indicator:focus {
  background-color: #141716;
}

.mln__toggle-indicator {
  z-index: 0;
  background-color: transparent;
  pointer-events: none;
}

.mln__toggle-btn__chevron {
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  margin-top: -0.156rem;
  margin-left: -0.344rem;
  width: 0.75rem;
  height: 0.563rem;
  transition: transform 100ms ease-in-out;
}
.mln__toggle-btn__chevron:before, .mln__toggle-btn__chevron:after {
  position: absolute;
  top: 0.125rem;
  display: block;
  width: 0.5rem;
  height: 0;
  border-top: 0.188rem solid white;
  border-radius: 1px;
  content: "";
  transition: transform 150ms ease-in-out;
  -webkit-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.mln__toggle-btn__chevron:before {
  left: 0;
  -webkit-transform: rotate(45deg) translateZ(0);
  -ms-transform: rotate(45deg) translateZ(0);
  transform: rotate(45deg) translateZ(0);
}
.mln__toggle-btn__chevron:after {
  right: 0;
  -webkit-transform: rotate(-45deg) translateZ(0);
  -ms-transform: rotate(-45deg) translateZ(0);
  transform: rotate(-45deg) translateZ(0);
}

.mln__has-child--showing > .mln__child-controls .mln__toggle-btn__chevron:before {
  left: 0;
  -webkit-transform: rotate(-45deg) translateZ(0);
  -ms-transform: rotate(-45deg) translateZ(0);
  transform: rotate(-45deg) translateZ(0);
}
.mln__has-child--showing > .mln__child-controls .mln__toggle-btn__chevron:after {
  right: 0;
  -webkit-transform: rotate(45deg) translateZ(0);
  -ms-transform: rotate(45deg) translateZ(0);
  transform: rotate(45deg) translateZ(0);
}
.mln__has-child--showing > .mln__child-controls .mln__toggle-btn__symbol-custom {
  display: inline-block;
  -webkit-transform: rotate(-180deg);
  -ms-transform: rotate(-180deg);
  transform: rotate(-180deg);
}

@media (min-width: 768px) {
  .mln--navbar .mln__expand-btn {
    display: none;
  }
  .mln--navbar .mln__toggle-btn,
.mln--navbar .mln__toggle-indicator {
    background-color: transparent;
  }
  .mln--navbar .mln__toggle-btn:hover,
.mln--navbar .mln__toggle-indicator:hover {
